import { useState, createContext } from 'react';
import { singleProjectData as singleProjectDataJson } from '../data/singleProjectData';
import { reboutementProjectData as reboutementProjectDataJson } from '../data/ReboutementProjectData';
import { magnetismeProjectData as magnetismeProjectDataJson } from '../data/MagnetismeProjectData';
import { reikiProjectData as reikiProjectDataJson } from '../data/ReikiProjectData';
import { coupeFeuProjectData as coupeFeuProjectDataJson } from '../data/CoupeFeuProjectData';
import { guerisseurProjectData as guerisseurProjectDataJson } from '../data/GuerisseurProjectData';
import { energeticienneProjectData as energeticienneProjectDataJson } from '../data/EnergeticienneProjectData';
// Importez d'autres données de projet si nécessaire...

// Créez un objet pour mapper les projectID aux données
const projectDataMap = {
    "1": reboutementProjectDataJson,
    "2": magnetismeProjectDataJson,
	"3": reikiProjectDataJson,
	"4": coupeFeuProjectDataJson,
	"5": guerisseurProjectDataJson,
	"6": energeticienneProjectDataJson,
    // Ajoutez d'autres projets ici...
    // "x": yetAnotherProjectDataJson,

};

const SingleProjectContext = createContext();

export const SingleProjectProvider = ({ children, projectID }) => {
    // Récupérez les données du projet à partir de la map
    const initialProjectData = projectDataMap[projectID] || 1; // null si le projectID n'est pas trouvé

    const [ProjectData, setProjectData] = useState(initialProjectData);

    return (
        <SingleProjectContext.Provider value={{ ProjectData, setProjectData }}>
            {children}
        </SingleProjectContext.Provider>
    );
};

export default SingleProjectContext;

